.root {
  display: inline-flex;
  width: 100%; }

.details {
  width: calc(100% - 264px);
  padding-right: 10%; }

.header {
  padding: 1.5rem 0 1.5rem 0;
  margin: 0 0 1.5rem 0; }

.name {
  color: rgba(0, 0, 0, 0.72);
  margin: 0 0 0.75rem 0; }

.role {
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.44); }

.picture {
  max-width: fit-content;
  height: fit-content;
  position: sticky;
  top: 48px; }

.profileImageContainer {
  margin-top: 0;
  width: 264px;
  height: 264px; }

.subheading {
  color: rgba(0, 0, 0, 0.72);
  margin: 0 0 1.5rem 0; }

.profileImage {
  border-radius: 8px; }

.personalInformation {
  padding: 1.5rem 1.5rem 1.5rem 1.5rem;
  border: 1px solid rgba(0, 0, 0, 0.08);
  border-radius: 3px; }

.profileInfo {
  padding: 0.75rem 0 0.75rem 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.08);
  display: flex;
  width: 100%;
  align-items: baseline;
  justify-content: flex-start; }

.infoPoint {
  flex: 1; }

.infoValue {
  flex: 1.68;
  text-transform: capitalize; }

.ktpImage {
  border-radius: 3px; }

.validateUser {
  padding-top: 1.5rem;
  width: 100%; }

.listContainer {
  display: inline-flex;
  align-items: baseline;
  justify-content: flex-end; }

.list {
  margin: 0 0.75rem 0 0;
  color: rgba(0, 0, 0, 0.54); }

.point {
  color: rgba(0, 0, 0, 0.72); }

.smallHeading {
  color: rgba(0, 0, 0, 0.72); }

.activitesHeader {
  display: inline-flex;
  justify-content: space-between;
  align-items: baseline;
  width: 100%;
  margin: 0 0 2rem 0; }
