.root {
  padding: 0 0 1.5rem 0; }

.pageHeading {
  margin: 2rem 0 2rem 0; }

.subheading {
  margin: 0 0 0.75rem 0; }

.panels {
  padding: 3rem 0 3rem 0; }

.badge {
  margin-left: 0.5rem; }

.mainContainer {
  padding-top: 2rem; }

.navTabContainer {
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  box-shadow: 0px 1px 0px 0px rgba(0, 0, 0, 0.08); }

.navtab {
  cursor: pointer;
  padding: 1.5rem 0 1.5rem 0;
  color: rgba(0, 0, 0, 0.72);
  margin: 0 2rem 0 0; }

.activeNavTab {
  color: #008380;
  box-shadow: 0px 1px 0px 0px #008380, inset 0px -1px 0px 0px #008380; }
